import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { nextPage } from "../../../Utils/Functions/nextPage";
import RequiredDocumentDetailCard from "./RequiredDocumentDetailCard";
import style from "../../FinanceManager/FinanceManager.module.css";
import { useDocumentNotFilledStudentList } from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
const RequiredDocumentDetail = ({ aid }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const {
    documentNotFilledStudentList,
    documentNotFilledStudentListLoading,
    documentNotFilledStudentListRefetch,
  } = useDocumentNotFilledStudentList({
    data: {
      aid: aid,
      did: getQuery.state?.docs?._id,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !getQuery.state?.docs?._id ? !getQuery.state?.docs?._id : !aid,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (documentNotFilledStudentList?.list?.length) {
      let obj = nextPage(documentNotFilledStudentList?.list?.length);
      setState(() => obj);
    }
  }, [documentNotFilledStudentList?.list]);

  const onSearchEvent = (e) => {
    if (
      !documentNotFilledStudentListLoading &&
      documentNotFilledStudentList?.list?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };

  const onRefetch = useCallback(() => {
    setPage((prev) => (prev = 1));
    documentNotFilledStudentListRefetch();
  }, [documentNotFilledStudentListRefetch]);

  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{getQuery.state?.docs?.document_name ?? ""}</h6>
            <div className={style.fm_search}>
              <section className={style.fm_search_container}>
                <div className={style.fm_search_container_inner}>
                  <img
                    className={style.fm_search_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  <input
                    type="text"
                    placeholder={t("search")}
                    onChange={onSearchEvent}
                    style={{
                      minWidth: "320px",
                    }}
                    onKeyUp={onCallBindSearch}
                    value={searchCopy}
                  />
                </div>
              </section>
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />

        <UserMemberContentWrapper>
          {documentNotFilledStudentList?.list?.map((docs, index) =>
            documentNotFilledStudentList?.list?.length === index + 1 ? (
              <div key={docs?._id} ref={ref}>
                <RequiredDocumentDetailCard
                  docs={docs}
                  docId={getQuery.state?.docs?._id}
                  onRefetch={onRefetch}
                />
              </div>
            ) : (
              <RequiredDocumentDetailCard
                docs={docs}
                key={docs?._id}
                docId={getQuery.state?.docs?._id}
                onRefetch={onRefetch}
              />
            )
          )}
          {documentNotFilledStudentListLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <>{page === 1 ? <QvipleLoader /> : <Loading />}</>
            )
          ) : search ? (
            documentNotFilledStudentList?.list?.length === 0 && (
              <EmptyMessage title="No student found related this search." />
            )
          ) : (
            documentNotFilledStudentList?.list?.length === 0 && (
              <EmptyMessage title="No student." />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
    </>
  );
};

export default RequiredDocumentDetail;
