import { useTranslation } from "react-i18next";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import { useState } from "react";
import CreateButton from "../../../Ui/Button/CreateButton";
import style from "../AdmissionManager.module.css";
import {
  assestsFeedUrl,
  assestsUserAdmissionUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useDocumentNotFilledStudentAction } from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import QvipleLoader from "../../../Loader/QvipleLoader";
const RequiredDocumentDetailCard = ({ docs, docId, onRefetch }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [documentNotFilledStudentAction] = useDocumentNotFilledStudentAction();
  const onAction = (val) => {
    if (docId && docs?.student?._id && val) {
      setDisabled((pre) => !pre);
      documentNotFilledStudentAction({
        did: docId,
        sid: docs?.student?._id,
        bodyParameter: {
          status: val,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  return (
    <>
      <div className={style.apf_container} title="View Profile">
        <div className={style.apf_container_inner}>
          <img
            alt="Student Avatar"
            src={
              docs?.student?.studentProfilePhoto
                ? `${imageShowUrl}/${docs?.student?.studentProfilePhoto}`
                : `${assestsFeedUrl}/feed-user-avatar.svg`
            }
            loading="lazy"
          />
          <div className={style.apfci_text}>
            <h6>{`${docs?.student?.studentFirstName ?? ""} ${
              docs?.student?.studentMiddleName
                ? docs?.student?.studentMiddleName
                : ""
            } ${docs?.student?.studentLastName ?? ""}`}</h6>

            <p>
              {t("gr_no")}
              {docs?.student?.studentGRNO ?? ""}
              {docs?.student?.department?.dName
                ? `, ${docs?.student?.department?.dName}`
                : ""}
            </p>
          </div>
        </div>
        <div className={style.apfci_text}>
          {docs?.collect?.not_filled === "Yes" ? (
            <img
              src={`${assestsUserAdmissionUrl}/uncheck.svg`}
              alt="check icon"
              onClick={() => onAction("No")}
              style={{
                cursor: "pointer",
              }}
            />
          ) : (
            <img
              src={`${assestsUserAdmissionUrl}/check.svg`}
              alt="check icon"
              onClick={() => onAction("Yes")}
              style={{
                cursor: "pointer",
              }}
            />
          )}
        </div>
      </div>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default RequiredDocumentDetailCard;
